.TitlePremium{
    align-items: center;
    padding: 100px;
    font-size:42px;
    font-family: cursive;
    animation-name: animateDestacados;
    animation-iteration-count: infinite;
    animation-duration: 2s;
    cursor: pointer;
    width: auto;
    /* box-shadow: 0px 0px 10px 0px #333 inset; */
   
    
}
 

@keyframes animateDestacados{
    0%{color:  rgb(165, 37, 144)}
    50%{color: black;}
    100%{color: rgb(228, 23, 211);}
} 


